<template>
  <div class="demo-sticky-table">
    <div class="link-table">
      <div class="link-table-head"
           @mouseenter="()=>hoverPart = 'head'"
           @mousewheel="onHeadMousewheel"
      >
        <pl-scroll scrollX
                   fitContentHeight
                   ref="headScroll"
                   @scroll="e=>onScroll(e,'head')">
          <table>
            <thead>
            <tr>
              <component
                  v-for="(col,colIndex) in columns"
                  :key="col.field"
                  :is="col.fixed?'pl-scroll-sticky':'td'"

                  v-bind="col.fixed?{
                                    tag:'td',
                                    [col.fixed]:0,
                                }:{}"
                  :style="{width:col.width,backgroundColor:col.fixed?'#f2f2f2':'white'}">
                <div v-if="colIndex === 0">
                  <pl-toggle/>
                </div>
                <div v-else>
                  {{ col.title }}
                </div>
              </component>
            </tr>
            </thead>
          </table>
        </pl-scroll>
      </div>
      <div class="link-table-body" @mouseenter="()=>hoverPart = 'body'">
        <pl-scroll
            scrollX
            ref="bodyScroll"
            @scroll="e=>onScroll(e,'body')"
        >
          <table>
            <tbody>
            <tr v-for="(data,index) in list" :key="index">
              <component
                  v-for="(col,colIndex) in columns"
                  :key="col.field"
                  :is="col.fixed?'pl-scroll-sticky':'td'"

                  v-bind="col.fixed?{
                                    tag:'td',
                                    [col.fixed]:0,
                                }:{}"
                  :style="{width:col.width,backgroundColor:col.fixed?'#f2f2f2':'white'}">

                <div v-if="colIndex === 0 || colIndex=== columns.length-1">
                  <pl-toggle/>
                </div>
                <div v-else>
                  {{ data[col.field] }}
                </div>
              </component>
            </tr>
            </tbody>
          </table>
        </pl-scroll>
      </div>
    </div>
  </div>
</template>

<script>

import list from '../data/data-1.json'


export default {
  name: "demo-sticky-table",
  data() {

    const width = '100px'

    return {
      list,
      columns: [
        {field: 'name', title: 'name', width: width, fixed: 'left'},
        {field: 'size', title: 'size', width: width},
        {field: 'id', title: 'id', width: width},
        {field: 'date', title: 'date', width: width},
        {field: 'color', title: 'color', width: width},

        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},
        {field: 'color', title: 'color', width: width},

        {field: 'star', title: 'star', width: width, fixed: 'right'},
      ],
      hoverPart: null,
    }
  },
  methods: {
    onScroll(e, tag) {
      if (tag !== this.hoverPart) {
        return
      }
      this.$refs[`${tag === 'head' ? 'body' : 'head'}Scroll`].methods.scrollLeft(e.target.scrollLeft)
    },
    onHeadMousewheel(e) {
      e.preventDefault()
      this.$refs.headScroll.methods.scrollLeft(this.$refs.headScroll.state.wrapperScrollLeft + (e.deltaX || e.deltaY))
    },
  }
}
</script>

<style lang="scss">
.link-table {
  width: 100%;

  .link-table-head {
    width: 100%;
  }

  .link-table-body {
    height: 300px;
    overflow: hidden;
  }

  table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    border-spacing: 0;
    box-sizing: border-box;
    list-style: none;
    z-index: 0;

    td {
      background-color: white;
      padding: 0 16px;
      box-sizing: border-box;
      height: 40px;

      &:not(.pl-scroll-sticky) {
        position: relative;
      }

      &:after {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        background-color: #ddd;
        content: '';
        transform: scaleY(0.5);
      }
    }
  }

  .pl-scroll-content {
    min-width: 100%;
  }

  .pl-vertical-scrollbar-wrapper {
    z-index: 10;
  }
}
</style>